.cartitems{
    padding: 50px 30px;
    background: linear-gradient(180deg,#c7a4dc,#c7f3f54c 60%);
}
.cartitems hr{
    height: 3px;
    background: #757171;
    border: 0;
}
.cartitems-format-main{
    display: grid;
    grid-template-columns: 0.5fr 1fr 0.5fr 0.5fr 0.5fr 0.5fr;
    align-items: center;
    gap: 35px;
    color: #454545;
    font-size: 18px;
    font-weight: 600;
}
.cartitems-format{
    font-size: 17px;
    font-weight:500 ;
}
.carticon-prodcut-icon{
    height:35px ;
}
.cartitems-remove-icon{
    width: 15px; 
    margin: 0px 40px;
    cursor: pointer;
}
.cartitems-quantity{
    width: 64px;
    height: 50px;
    border: 2px solid #ebebeb;
    background: #fff;
}
.cartitems-down{
    display: flex;
    margin: 30px 0px;
}
.cartitems-total{
    flex: 1;
    display: flex;
    flex-direction: column;
    margin-right: 150px;
    gap: 20px;
}
.cartitems-total-item{
    display: flex;
    justify-content: space-between;
    padding: 30px ;  
}
.cartitems-total-item p{
    padding: 5px;
}
.free{
    padding-left: 20px;
}
.cartitems-total button{
    width: 262px;
    height:58px ;
    outline: none;
    border: none;
    background: #ff5a5a;
    color: #fff;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
}
.cartitems-promocode{
    flex: 1;
    font-size: 16px;
    font-weight: 500;
}
.cartitems-promocode p{
    color: #555;
}
.cartitems-promobox{
    width: 504px;
    margin-top: 10px;
    padding-left: 20px;
    height: 58px;
    background: #e2d7d7;
}
.cartitems-promobox input{
    border: none;
    outline: none;
    background: transparent;
    font-size: 16px;
    width: 330px;
    height: 50px;
}
.cartitems-promobox button{
    width: 170px;
    height: 58px;
    font-size: 16px;
    background: black;
    color: white;
    cursor:pointer;
}
@media(max-width:1280px){
    .cartitems-format-main{
        grid-template-columns: 0.5fr 3fr 0.5fr 0.5fr  0.5fr 0.5fr;
        padding: 15px 0px;
        font-size: 15px;
    }
    .carticon-prodcut-icon{
        height: 50px;
    }
    .cartitems-remove-icon{
        margin: auto;
    }
    .cartitems-quantity{
        width: 40px;
        height: 30px;
    }
    .cartitems-down{
        margin: 50px 0px;
        flex-direction: column;
        gap: 50px;
    }
    .cartitems-total{
        margin: 0;
    }
    .cartitems-total button{
        max-width: 200px;
        height: 45px;
        font-size: 13px;
    }
    .cartitems-promobox{
        width: auto;
        max-width:500px;
    }
    .cartitems-promobox input{
        width: 100%;
    }
    .cartitems-promobox button{
        width: 120px;
        margin-left: -125px;
    }
}
@media(max-width:500px){
    .cartitems-format-main{
        display: none;
        grid-template-columns: 0.5fr 3fr 0.5fr;
        gap: 10px;
    }
    .cartitems-format{
       display: grid;
    }
}