.popular{
    display: flex;
    flex-direction: column;
    align-items: center;
    background: linear-gradient(180deg,#e3caf1,#e9f8f94c 60%);
}
.popular h1{
    margin-top: 10px;
    color: #171717;
    font-size: 40px;
    font-weight: 600;
}
.popular hr{
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background: #171717;
}
.popular-item{
    display: flex;
    flex-direction: row;
}
@media(max-width:1280px){
    .popular{
        height: 70vh;
    }
    .popular h1{
        font-size: 40px;
    }
    .popular hr{
        width: 200px;
    }
    .popular-item{
        gap:20px;
        margin-top: 20px;
    }
}
@media(max-width:1024px){
    .popular{
        height: 60vh;
    }
    .popular h1{
        font-size: 30px;
    }
    .popular hr{
        width: 120px;
        height: 3px;
    }
    .popular-item{
        gap:15px;
        margin-top: 20px;
    }
}
@media(max-width:800px){
    .popular{
        height: 50vh;
        gap:6px;
    }
    .popular h1{
        font-size: 20px;
    }
    .popular hr{
        width: 100px;
    }
    .popular-item{
        gap:5px;
    }
}    
@media(max-width:500px){
    .popular{
        height: 90vh;
    }
    .popular-item{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
    }
}